<template>
    <div class="group" v-loading="loading">
        <div class="group_head">
            <div>{{ lang.tips8 }} ({{ group.length }})</div>
            <el-button type="primary" size="small" icon="el-icon-plus" @click="addProject" :disabled="isButtonDisabled"
                style="background: #345CA0;border-radius: 12px;padding: 15px 24px;border-width: 0px;">
                {{ lang.add }}
            </el-button>
        </div>
        <div class="group_item">
            <div class="nodata" v-if="group.length == 0"><el-empty :description="lang.nodata"></el-empty></div>
            <div class="box" v-for="item in group" :key="item.id">
                <div class="main" @click="goProject(item.id)">
                    <div class="cover" v-loading="item.loading" :element-loading-text="lang.loading + '...'">
                        <img class="project_cover" :src="'https://www.simplerevise.com/img/project/v2/thumbnail/' + item.cover + '.jpg'" alt="">
                    </div>
                </div>
                <div class="title">
                    <div class="action">
                        <i class="el-icon-share" @click="share(item.id)"></i>
                        <i class="el-icon-edit" @click="rename(item.id)"></i>
                        <i class="el-icon-delete" @click="del(item.id)"></i>
                    </div>
                    <p>{{ item.group_name }}</p>
                    <p>{{ item.user_name }} Â· {{ item.create_time }}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import timer from '../tools/timer';
import global from '@/tools/global';
export default {
    name: 'group',
    data() {
        return {
            user: {},
            group: [],
            loading: true,
            global,
            httping: false,
            lang: {}
        }
    },
    created() {
        this.lang = this.$lang('group');
        this.user = JSON.parse(localStorage.getItem('designer_user'));

        // æ²¡æåä¸é¡¹ç®
        if (this.user.projects.substring(1).length == 0) {
            this.group = [];
            this.loading = false;
            return;
        }

        this.getgroup();

    },
    computed: {
        isButtonDisabled() {
            const user = JSON.parse(localStorage.getItem('designer_user'));
            const userProjects = user.projects.substring(1);
            const numProjects = userProjects.length > 0 ? userProjects.split(',').length : 0;

            // Check if any item in the group array has reached its maximum projects
            const isAnyItemMaxedOut = this.group.some(item => item.max_projects <= numProjects);

            return isAnyItemMaxedOut;
        }
    },
    methods: {
        // è·åé¡¹ç®ç»åè¡¨
        getgroup() {
            // æ ¹æ®idåè¡¨è·åé¡¹ç®ä¿¡æ¯
            this.user.projects = this.user.projects.substring(1);
            if (this.user.projects[0] == ',') {
                this.user.projects = this.user.projects.substring(1);
            }
            this.$ajax.get('/group/list/?ids=' + this.user.projects + '&createrId=' + this.user.id).then(r => {
                this.group = r.map(item => {
                    item.create_time = timer.time('y-m-d', item.create_time * 1000);
                    item.loading = true;
                    return item;
                });

                // å¤çå°é¢å±ç¤ºæ¹å¼ï¼æ¨ªå¾ = rowï¼ç«å¾ = col
                this.$nextTick(() => {
                    let imgs = document.querySelectorAll('.project_cover');
                    imgs.forEach((item, index) => {
                        let img = new Image();
                        img.onload = () => {
                            this.group[index].loading = false;
                            if (img.width > img.height) {
                                item.classList.add('row');
                            } else {
                                item.classList.add('col');
                            }
                        }
                        img.src = item.src;
                    })
                    this.loading = false;
                })
            })
        },
        // åäº«é¡¹ç®å
        share(id) {
            this.group.map(item => {
                if (item.id == id) {
                    this.$copy(id, item.group_name);
                }
            })
        },
        // éå½å
        rename(id) {
            let that = this;
            that.$prompt(that.lang.tips2, that.lang.tips1, {
                inputPattern: /\S/,
                inputErrorMessage: that.lang.tips5,
                closeOnPressEscape: false,
                closeOnClickModal: false,
                cancelButtonText: that.lang.tips3,
                confirmButtonText: that.lang.tips4,
                beforeClose(action, instance, done) {
                    if (action == 'confirm') {
                        if (that.httping) return;
                        that.httping = true;
                        that.$ajax.post('/group/rename', {
                            id,
                            name: instance.inputValue
                        }).then(r => {
                            that.$message.success('success');
                            that.group.map(item => {
                                if (item.id == id) {
                                    item.group_name = instance.inputValue;
                                }
                            })
                            done();
                            setTimeout(() => {
                                that.httping = false;
                            }, 300)
                        })
                    } else {
                        done();
                    }
                }
            }).catch(() => { });
        },
        // å é¤é¡¹ç®
        del(id) {
            let that = this;
            this.$confirm(that.lang.tips7, that.lang.tips6, {
                type: 'warning',
                closeOnPressEscape: false,
                closeOnClickModal: false,
                cancelButtonText: that.lang.tips3,
                confirmButtonText: that.lang.tips4,
                beforeClose(action, instance, done) {
                    if (action == 'confirm') {
                        if (that.httping) return;
                        that.httping = true;
                        let index = 0;
                        that.group.some((item, i) => {
                            if (item.id == id) {
                                index = i;
                                return true;
                            }
                        });
                        that.$ajax.get('/group/del/?id=' + id + '&createrId=' + that.user.id).then(r => {
                            that.group.splice(index, 1);
                            that.$message.success('success');
                            done();
                            setTimeout(() => {
                                that.httping = false;
                            }, 300);
                            // Update the user's projects and trigger computed property update
                            that.updateUserProjects();
                        });
                    } else {
                        done();
                    }
                }
            }).catch(() => { });
        },

        // Update user's projects and trigger computed property update
        updateUserProjects() {
            const user = JSON.parse(localStorage.getItem('designer_user'));
            const projectIds = this.group.map(item => item.id).join(',');
            user.projects = ',' + projectIds;
            localStorage.setItem('designer_user', JSON.stringify(user));
        },
        // æ¥çé¡¹ç®
        goProject(id) {
            this.$router.push({
                path: '/list',
                query: { id }
            })
        },
        // æ·»å æ°é¡¹ç®
        addProject() {
            let that = this;
            that.$prompt(that.lang.tips5, that.lang.add, {
                inputPattern: /\S/,
                inputErrorMessage: that.lang.tips5,
                closeOnPressEscape: false,
                closeOnClickModal: false,
                cancelButtonText: that.lang.tips3,
                confirmButtonText: that.lang.tips4,
                beforeClose(action, instance, done) {
                    if (action == 'confirm') {
                        if (that.httping) return;
                        that.httping = true;
                        that.$ajax.post('/group/add', {
                            name: instance.inputValue,
                            createrId: JSON.parse(localStorage.getItem('designer_user')).id
                        }).then(r => {
                            done();
                            that.setGroup(r);
                            that.$message.success('success');
                            setTimeout(() => {
                                that.httping = false;
                            }, 300)
                        })
                    } else {
                        done();
                    }
                }
            }).catch(() => { });
        },
        // æ´æ°é¡¹ç®id
        setGroup(r) {
            this.user.projects = r;
            localStorage.setItem('designer_user', JSON.stringify(this.user));
            this.getgroup();
        }
    }
}
</script>

<style lang="less" scoped>
@import url('../tools/common.less');

.group {
    width: calc(100% - 60px);
    height: 100%;
    padding: 0 30px;
    display: flex;
    justify-content: flex-start;
    align-content: flex-start;
    flex-wrap: wrap;
    overflow-x: hidden;
    overflow-y: auto;

    .group_head {
        width: calc(100% - 20px);
        height: 40px;
        padding: 20px 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        div {
            font-weight: 700;
            font-size: 24px;
            line-height: 29px;
        }
    }

    .group_item {
        width: 100%;
        height: calc(100% - 80px);
        display: flex;
        justify-content: flex-start;
        align-content: flex-start;
        flex-wrap: wrap;
        overflow-x: hidden;
        overflow-y: auto;

        .nodata {
            width: 100%;
            height: 70%;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .box {
            width: 230px;
            //height: 265px;
            background: #FFFFFF;
            border-radius: 12px;
            margin: 10px;
            padding: 15px;
            box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.0500);

            .main {
                width: 230px;
                height: 230px;
                background: #F5F5F7;
                border-radius: 8px;
                transition: all 0.3s;
                cursor: pointer;

                .cover {
                    width: 230px;
                    height: 230px;

                    img.row {
                        width: 100%;
                        border-radius: 8px;
                    }

                    img.col {
                        height: 100%;
                        border-radius: 8px;
                    }
                }
            }

            .title {
                text-align: left;
                font-weight: 600;
                font-size: 14px;
                color: #000000;
                margin-top: 15px;
                position: relative;

                p {
                    line-height: 25px;
                }

                p:last-child {
                    font-weight: 400;
                    font-size: 12px;
                    color: #86868B;
                }

                .action {
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translateX(-50%) translateY(-50%);
                    -webkit-transform: translateX(-50%) translateY(-50%);
                    transition: all ease 0.35s;
                    width: 100%;
                    z-index: 2;
                    text-align: center;
                    opacity: 0;

                    i {
                        font-size: 14px;
                        color: #000000;
                        cursor: pointer;
                        border-radius: 50%;
                        padding: 8px;
                        margin: 0px 10px;
                        background-color: #FFFFFF;
                        box-shadow: 0px 12px 16px -4px rgba(16, 24, 40, 0.08), 0px 4px 6px -2px rgba(16, 24, 40, 0.03);
                    }
                }

                &:before {
                    background: #FFFFFF;
                    content: '';
                    height: 100%;
                    left: 0;
                    opacity: 0;
                    position: absolute;
                    top: 0;
                    transition: all ease 0.35s;
                    width: 100%;
                }
            }

            &:hover {
                .title .action {
                    opacity: 1;
                }

                .title:before {
                    opacity: 0.9;
                }
            }
        }
    }

    .group_item::-webkit-scrollbar {
        display: none;
    }
}
</style>